nav{
    display: flex;
    justify-content: center;
    height: 70px;
    width: 100%;
    background-color: rgb(97, 52, 32);
  }
  .navWidth{
    position: relative;
    display: flex;
    justify-content: space-between;
    width: 1000px;
    height: 100%;
    padding: 0 10px;
  }
  .logoDiv{
      max-width: inherit;
      display: flex;
  }
  .logo{
      display: inline-block;
  }
  .logoFont{
      display: inline;
      color: white;
      margin-left: 20px;
      font-size: 50px;
      line-height: 70px;
      cursor: pointer;
  }
  .ResterOrLogIn, .userInNaV{
      display: flex;
      align-items: center;
  }
  .NavForms{
      position: absolute;
      bottom: 0;
      transform: translateY(100%);
      right: 0;
      padding: 10px;
      background-color: rgb(201, 226, 101);
      width: 200px;
  }
  input{
      width: 100%;
      padding: 5px 10px;
      border-radius: 20px;
      font-size: 15px;
  }
  label{
      display: block;
      margin-left: 10px;
  }
  .submit{
      border-radius: 0;
      margin-top: 10px;
      background-color: rgb(97, 52, 32);
      color: white;
      font-size: 17px;
  }
  .navFont{
      font-size: 20px;
      color:rgb(201, 226, 101);
  }
  button:hover{
      filter: brightness(1.3);
  }
   @media screen and (max-width:600px){
       .logoFont{
           display: none;
       }
   }